/* breadcrumb */
.content-feature {
  padding: 46px 0;
  margin: 0 auto;

  .list_path {
    overflow: hidden;

    li {
      float: left;
      font-size: 12px;
      line-height: 22px;
    }

    .link_path {
      color: #194BDC
    }
  }

  .ico_arrowright {
    display: inline-block;
    width: 16px;
    height: 16px;
    margin: 3px 8px;
    background: url(/assets/images/ico_arrowright.svg) no-repeat;
    vertical-align: top;
  }
}
