.doc-header {
  position: fixed;
  display: flex;
  width: 100%;
  height: 56px;
  min-width: 910px;
  background-color: #000;
  justify-content: space-between;
  z-index: 99;

  .wrap_head {
    padding-left: 32px;

    .doc-title {
      overflow: hidden;
      color: #fff;
      height: 32px;
      margin-top: 16px;

      .link_tit {
        float:left;
        font-size:0;
      }

      .ico_cloud {
        display:inline-block;
        width:32px;
        height:32px;
        margin-top: -4px;
        background: url($baseurl + '/assets/images/cloud_logo.png') no-repeat;
        vertical-align: top;
      }

      .logo_cloud {
        display: inline-block;
        width: 112px;
        height: 15px;
        margin-top: 4px;
        margin-left: 10px;
        background: url($baseurl + '/assets/images/logo.svg') no-repeat;
        vertical-align: top;
      }

      .txt_bar {
        float:left;
        width:1px;
        height:13px;
        margin: 7px 16px 0 10px;
        background-color: #fff;
        opacity: 0.6;
      }

      .link_subtit {
        float: left;
        margin-top: 4px;
        font-size: 20px;
        line-height: 20px;
        color: #fff;
        font-weight: normal;
      }
    }
  }

  .btn_menu {
    display: none;
  }

  .form_search {
    position:relative;
    width: 280px;
    height: 32px;
    margin: 12px 32px 0 0;
    z-index: 100;

    .inp_search {
      width: 236px;
      height: 16px;
      padding: 8px 28px 8px 16px;
      border: 0 none;
      background-color: rgba(255, 255, 255, 0.1);
      color: #fff;
      font-size:12px;
      line-height: 16px;
      border-radius: 4px;
      outline: none;

      &:-ms-input-placeholder,
      &::-webkit-input-placeholder,
      &::-moz-placeholder {
        color: #fff;
        opacity: 0.5;
      }
    }

    .btn_search {
      position:absolute;
      width: 24px;
      height: 24px;
      margin: 4px 4px 4px 0;
      background: url($baseurl + '/assets/images/ico_search.svg') no-repeat;
      font-size:0;
      line-height: 0;
      text-indent: -9999px;
      top:0;
      right:0;
    }
  }
}

@media screen and (max-width:1189px) {
  .doc-header {
    .wrap_head {
      .doc-title {
        margin-left: 40px
      }
    }

    .btn_menu {
      display: block;
      position: fixed;
      top:12px;
      left:20px;
      z-index: 100;

      .ico_menu {
        display:block;
        background: url($baseurl + '/assets/images/ico_menu.svg') no-repeat;
        width: 32px;
        height: 32px;
        font-size: 0;
        line-height: 0;
        text-indent: -9999px;
      }
    }
  }
}
