.doc-footer {
  margin-left: 280px;

  .inner_footer {
    margin: 0 auto;
    width: 1230px;
    padding: 80px 40px 32px;
    box-sizing: border-box;

    .txt_copyright {
      font-size:12px;
      color: #909090;
    }
  }
}

// 1680px 이하
@media screen and (max-width:1679px) {
  .doc-footer {
    .inner_footer {
      width: 910px;
    }
  }
}

@media screen and (max-width:1189px) {
  .doc-footer {
    margin-left: 0;
  }
}
