/* search result */
.section_result {
  padding-top: 94px;

  .title_result {
    font-size: 32px;
    line-height: 48px;
    letter-spacing: -0.3px;
    font-weight: normal;

    .txt_query {
      font-weight: bold;
    }
  }

  .link_result {
    display: block;
  }

  .tit_result {
    font-size: 24px;
    line-height: 36px;
    color: #194BDC;
    letter-spacing: -0.3px;
  }

  .desc_result {
    margin-top: 18px;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: -0.3px;
  }

  .txt_target {
    color: #194BDC;
  }

  .list_search {
    margin-top: 36px;

    li {
      position: relative;
      padding: 40px 0;

      &::before {
        position: absolute;
        top:0;
        left:0;
        right:0;
        height:1px;
        background-color: #222;
        opacity: 0.1;
        content: "";
      }

      &:last-child::after {
        position: absolute;
        bottom:0;
        left:0;
        right:0;
        height:1px;
        background-color: #222;
        opacity: 0.1;
        content: "";
      }

      .path_search {
        display: block;
        margin-top: 12px;
        font-size:13px;
        line-height:20px;
        letter-spacing: -0.2px;
        color: #222;
        opacity: 0.5;
      }
    }
  }
}

.paging_result {
  padding-top: 40px;
  text-align: center;
  line-height: 0;

  .inner_paging {
    display: inline-block;
    overflow: hidden;

    .link_paging {
      float: left;
      width:32px;
      height:32px;
      line-height:34px;
      color: #222;

      &.current {
        color: #2288FF;
        background-color: #F6F6FF;
        border-radius: 4px
      }
    }

    .btn_paging {
      display:block;
      float:left;
      width:32px;
      height:32px;
      text-indent: -9999px;
      font-size: 0;

      &.btn_first {
        background: url(/assets/images/ico_first.svg) no-repeat;
      }
      &.btn_prev {
        background: url(/assets/images/ico_prev.svg) no-repeat;
        margin-right: 8px;
      }
      &.btn_next {
        background: url(/assets/images/ico_next.svg) no-repeat;
        margin-left: 8px;
      }
      &.btn_last {
        background: url(/assets/images/ico_last.svg) no-repeat;
      }
    }
  }
}
