.container-doc.main {
  width: 100%;
  background: url(../images/img_main.png) no-repeat;
  background-size: cover;

  .doc-main {
    .inner-main {
      width: 810px;
    }
  }
}

.article_index {
  margin-top: 240px;
  text-align: center;

  .tit_index {
    font-size: 40px;
    font-weight: bold;
    letter-spacing: -1px;
    color: #fff;
    font-family: "Noto Sans CJK KR", "sans-serif";
}

  .desc_index {
    margin-top: 16px;
    font-size: 16px;
    line-height:24px;
    color: #fff;
    opacity: 0.7;
    font-family: "Noto Sans CJK KR", "sans-serif";
  }

  .wrap_search {
    position: relative;
  }

  .form_search {
    position: relative;
    width: 688px;
    height: 56px;
    margin: 40px auto 0;

    &.write_on {
      .wrap_tag {
        display: none;
      }

      & + .list_suggest {
        display: block;
      }

      .btn_search {
        display: none;
      }

      .btn_reset {
        display: block;
        position: absolute;
        width: 32px;
        height: 32px;
        margin: 12px 4px 12px 0;
        background: url(../images/ico_reset.svg) no-repeat;
        background-position: 8px 8px;
        font-size: 0;
        line-height: 0;
        text-indent: -9999px;
        top: 0;
        right: 0;
      }
    }
  }

  .inp_search {
    width: 604px;
    height: 22px;
    padding : 17px 60px 17px 24px;
    border: 0 none;
    border-radius: 4px;
    outline: none;
    font-size: 16px;
    line-height: 22px;
    font-family: "Noto Sans CJK KR", sans-serif;
  }

  .wrap_tag {
    position: absolute;
    top: 0;
    right: 56px;
    font-size:0;
  }

  .tag_suggest {
    padding: 7px 12px;
    margin-top: 12px;
    background-color: rgba(25, 75, 200, 0.08);
    color: #194BDC;
    border-radius: 4px;
    font-size:12px;
    line-height:18px;
    font-family: "Noto Sans CJK KR", sans-serif;

    & + .tag_suggest {
      margin-left: 8px;
    }
  }

  .btn_reset {
    display: none;
  }

  .btn_search {
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    margin: 8px 8px 8px 0;
    background: url(../images/ico_mainsearch.svg) no-repeat;
    background-position: 8px 8px;
    font-size: 0;
    line-height: 0;
    text-indent: -9999px;
    top: 0;
    right: 0;
  }

  .list_suggest {
    display: none;
    position: absolute;
    width: 688px;
    margin: 8px auto 0;
    padding: 8px 0;
    text-align: left;
    background: #fff;
    border-radius: 4px;
    font-size:14px;
    line-height: 20px;
    top: 56px;
    left: 0;
    right: 0;
    z-index: 90;

    li {
      padding: 10px 15px;

      &:hover {
        background: rgba(25, 75, 220, 0.04);
      }
    }
  }
}

@media screen and (max-width:1189px) {
  .doc-main {
    margin-left: 0;
  }
}
