$category-selected-color: #194BDC;

/* nav */
.doc-gnb {
  position: fixed;
  overflow-y: scroll;
  top: 56px;
  bottom: 0;
  left: 0;
  width: 280px;
  border-right: 1px solid rgba(0, 0, 0, .1);
  box-sizing: border-box;
  z-index: 100;
  -ms-user-select: none;
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  background-color: #fff;

  > .list_category {
    font-size: 16px;
    margin-top: 8px;

    > li {
      padding: 6px 0;
      border-bottom: 1px solid rgba(0, 0, 0, .08);
    }

    /* 최상단 카테고리를 제외한 모든 하위 카테고리는 기본적으로 숨김 처리 */ 
    .list_category {
      display: none;
      font-size: 14px;
    }
  }

  li.on {
    > .list_category {
      display: block;
    }

    > .tit_category {
      font-weight: bold;
      color: $category-selected-color;
      
      > .icon_arrowright {
        transform: rotate(90deg);
      }
    }
  }

  .tit_category {
    display: flex;
    padding: 13px 42px 14px 24px;
  }

  .external_wrapper {
    display: block;
    position: relative;
  }

  .external_icon {
    position: absolute;
    top: 2px;
    right: 0;
  }

  .depth2  > li > .tit_category {
    padding-left: 44px;
  }

  .depth3  > li > .tit_category {
    padding-left: 64px;
  }

  .depth4  > li > .tit_category {
    padding-left: 84px;
  }

  .tit_category:hover {
    cursor: pointer
  }

  .icon_arrowright, .icon_dot {
    margin-right: 8px;
    margin-top: 8px;
  }
}

@media screen and (max-width:1189px) {
  .doc-gnb {
    display: none;
    top: 0;

    &.gnb_on {
      display: block;
    }
  }
}
