/* main */
.doc-main {
  position: relative;
  min-height: calc(100vh - 133px - 56px);
  margin-left: 280px;
  padding: 56px 0 0;

  .inner-main {
    position: relative;
    width: 1230px;
    margin: 0 auto;
    padding: 0 40px;
    padding-bottom: 134px;
    box-sizing: border-box;
  }

}

.wrap_home {
  position: absolute;
  width: 560px;
  height: 256px;
  top: 50%;
  left: 50%;
  margin-top: -128px;
  margin-left: -280px;
  background: powderblue;
  text-align: center;

  .tit_home {
    font-size: 40px;
  }

  .form_search {
    position: relative;
    width:450px;
    margin: 30px auto;

    .wrap_input {
      position: relative;

      .inp_search {
        width: 450px;
        height: 20px;
        padding: 10px 20px 10px 5px;
        border: 0 none;
        background-color: #EDF1FD;
      }
      /* suggestion result */
      #search-box-main-suggestion-list {
        background-color: white;
        .suggestion-active {
          background-color: yellow;
        }
      }
    }
    .btn_search {
      position: absolute;
      width: 40px;
      height: 40px;
      top: 0;
      right: 0;
      background: red;
    }
  }
}

// 1680px 이하
@media screen and (max-width:1680px) {}

// 1440px 이하
@media screen and (max-width:1440px) {
  .doc-main {
    .wrap_toc {
      display:none;
    }
  }
}

@media screen and (max-width:1024px) {
  .doc-main {
    margin-left: 0;
  }
}

.article_markdown {
  width: 830px;
}
