/* toc */
.wrap_toc {
  overflow: auto;
  position: fixed;
  top: 150px;
  width: 400px;

  .list_toc {
    font-size:12px;

    .item_toc {
      & > a {
        display: block;
        position:relative;
        margin-top: 8px;
        font-size: 13px;
        color: #222;
        opacity: 0.5;
        line-height:20px;
        letter-spacing: -0.2px;

        &.active {
          font-weight: bold;
          opacity: 1;
          &::before {
            position: absolute;
            top:0;
            bottom:0;
            left:0;
            width:2px;
            background-color: #194BDC;
            content:"";
          }
        }
      }

      &.item_h1 > a {
        padding-left: 8px;
        font-weight: bold;
      }
      &.item_h2 > a {
        margin-left: 16px;
        padding-left: 8px;
      }
      &.item_h3 > a {
        margin-left: 32px;
        padding-left: 8px;
      }

      &.item_h1 + .item_h1 {
        margin-top: 24px;
      }
    }

    & > .item_toc > a {
      border-top: 0 none;
    }
  }
}
