p {
  margin: 24px 0 0;
  font-size: 16px;
  line-height: 28px;
  color: #222;
  letter-spacing: -.3px;
}

a {
  color: #194bdc;
  word-break: break-all;
  :visited {
    color: #194bdc;
  }
}

img {
  vertical-align: top;
  max-width: 100%;
}

table {
  margin-top: 24px;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid #e8e8e8;
  font-size: 14px;
  line-height: 24px;
  color: #222;
  word-break: keep-all;
  text-indent: 0;
  border-collapse: collapse;
  border-spacing: 0;
  overflow: auto;

  th {
    padding: 12px 24px;
    border: 0;
    background-color: #f6f6f6;
  }

  td {
    padding: 16px 24px;
    border: 0;
    border-top: 1px solid #e8e8e8;
    word-break: keep-all;
    vertical-align: top;
    line-height: 24px;
  }
}

.hint {
  position: relative;
  padding: 20px 24px 20px 46px;
  margin: 24px 0 40px;
  border-radius: 0;
  border-width: 2px;
  font-size: 14px;
  line-height: 24px;
  border-left: 2px solid;

  p:first-child {
    margin-top: 0;
    line-height: 24px;
    font-size: 14px;
  }

  &::before {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 24px;
    left: 15px;
    content: '';
  }

  &.info {
    border-color: #194bdc;
    background-color: rgba(25,75,220,.04);
    &::before {
      background: url($baseurl + '/assets/icons/hint_information.svg')no-repeat;
    }
  }

  &.danger {
    background-color:rgba(220,25,25,.04);
    border-color:#dc1919;
    &::before {
      background: url($baseurl + '/assets/icons/hint_danger.svg')no-repeat;
    }
  }

  &.warning {
    background-color:rgba(251,192,45,.08);
    border-color:#fbc02d;
    &::before {
      background: url($baseurl + '/assets/icons/hint_warning.svg')no-repeat;
    }
  }
}

.hint code, .language-plaintext {
  display: inline-block;
  padding: 0 6px;
  font-size: 14px;
  line-height: 22px;
  color: #dc1919;
  border-radius: .25rem;
  background-color: #eee;
  text-indent: 0;
}

[class^=caption-] {
  display: block;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  margin: 24px 0 16px;

  .txt-bar {
    display: inline-block;
    height: 14px;
    width: 1px;
    background: #222;
    vertical-align: top;
    margin: 7px 4px 0;
  }
}

.caption-image {
  color: #a7a7a7;
  margin: 16px 0 40px;
  
  .txt-bar {
    background: rgba(34,34,34,.4);
  }
}

.tab-code {
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  border: 0;
  background-color: #e8e8e8;
  margin: 16px 0 40px;

  input {
    height: 0;
    width: 0;
    overflow: hidden;
    opacity: 0;
    position: absolute;
    box-sizing: border-box;
    padding: 0;
  }

  label {
    display: inline-block;
    padding: .5rem 1rem;
    border-bottom: 1px transparent;
    cursor: pointer;
    min-width: 128px;
    height: 40px;
    padding: 9px 24px;
    font-size: 14px;
    line-height: 22px;
    color: #5d5d5d;
    box-sizing: border-box;
    text-align: center;
  }

  input[type=radio]:checked + label {
    border: 0;
    font-weight: 700;
    color: #222;
    background-color: #f6f6f6;
  }

  .tabs-code-wrapper {
    order: 999;
    width: 100%;
    border-top: 1px solid #f8f9fa;
    display: none;
    overflow: hidden;
    background-color: #f6f6f6;
  }

  input[type=radio]:checked + label + .tabs-code-wrapper {
    display: block;
    padding: 36px 24px 24px;
    border-top: 0;
  }
}